import colorPicker from '@/components/colorPicker.vue'
import uniPopup from "@/components/uni-ui/uni-popup/uni-popup.vue"
export default {
	components: {
		colorPicker,
		uniPopup,
	},
	props: {
		// 显示的数据
		html: {
			type: String,
		},
		imageUploader: {
			type: Function
		},
		muiltImage: {
			type: Boolean,
			default: false
		},
		compressImage: {
			type: Boolean,
			default: false
		},
		previewMode: {
			type: Boolean,
			default: false
		},
		tools: {
			type: Array,
			default: function() {
				return ['bold', 'italic', 'underline', 'strike', 'align-left', 'align-center', 'align-right',
					'remove', 'font', 'color', 'backgroundColor', 'clear', 'preview', 'date', 'list-check', 'list-ordered',
					'list-bullet', 'undo', 'redo', 'outdent', 'indent', 'divider', 'h1', 'h2','h3','h4','h5','h6','sub',
					'super'
				]
			}
		}
	},
	data() {
		return {
			show: true,
			readOnly: false,
			formats: {},
			fontColor: "#000000",
			bgColor: "",
			color: "",
			colorPickerName: "",
			showColor: false,
			fontSizeRange: [10, 12, 14, 16, 18, 24, 32, 36, 42],
			showPreview: false,
			htmlData: "",
		}
	},
	mounted: function() {
		if (this.previewMode) {
			this.previewData(this.html)
		}
	},
	computed: {
		labelConfirm: function() {
			return this.showPreview ? "关闭" : "保存"
		},
		labelCancel: function() {
			return this.showPreview ? "" : "取消"
		}
	},
	methods: {
		openColor(e) {
			var name = e.currentTarget.dataset.name
			var color = this.formats[name]
			this.colorPickerName = name
			if (name == 'backgroundColor' && !color) {
				color = "#FFFFFF"
			}
			if (name == 'color' && !color) {
				color = "#000000"
			}
			this.color = color
			this.$refs.popup.open({
				type: "bottom"
			})
		},
		colorPop(e) {
			this.showColor = e.show
		},
		colorPopClose() {
			this.$refs.popup.close()
		},
		colorChanged(e) {
			let label = ''
			switch (this.colorPickerName) {
				case 'backgroundColor':
					if (e.color == '#FFFFFF') {
						e.color = ''
					}
					this.bgColor = e.color
					label = '背景色'
					break
				case 'color':
					this.fontColor = e.color
					label = '颜色'
					break
			}
			this.colorPopClose()
			this._format(this.colorPickerName, e.color, label + e.color)
		},
		readOnlyChange() {
			this.readOnly = !this.readOnly
		},
		onEditorReady() {
			uni.createSelectorQuery().in(this).select('#editor').context((res) => {
				this.editorCtx = res.context
				if (this.html) {
					this.editorCtx.setContents({
						html: this.html
					})
				}
			}).exec()
		},
		undo() {
			this.editorCtx.undo()
			this.toast("撤销")
		},
		redo() {
			this.editorCtx.redo()
			this.toast("恢复")
		},
		format(e) {
			let {
				name,
				value,
				label
			} = e.target.dataset
			if (!name) return
			this._format(name, value, label)
		},
		_format(name, value, label) {
			this.editorCtx.format(name, value)
			this.toast(label)
		},
		toast(label) {
			uni.showToast({
				duration: 600,
				icon: 'none',
				title: label
			})
		},
		onStatusChange(e) {
			const formats = e.detail
			this.formats = formats
		},
		insertDivider() {
			const _this = this
			this.editorCtx.insertDivider({
				success: function() {
					_this.toast("插入分割线")
				}
			})
		},
		clear() {
			this.editorCtx.clear({
				success: (res) => {
					this.toast("清空")
				}
			})
		},
		removeFormat() {
			this.editorCtx.removeFormat()
			this.toast("清除格式")
		},
		insertDate() {
			const date = new Date()
			const formatDate = `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`
			this.editorCtx.insertText({
				text: formatDate
			})
			this.toast("插入日期")
		},
		insertImage() {
			let params = {}
			params.count = this.muiltImage ? 9 : 1
			params.sizeType = this.compressImage ? ['compressed'] : ['original']
			uni.chooseImage({
				...params,
				success: (res) => {
					res.tempFilePaths.map(path => {
						this.imageUploader(path, (url) => {
							this.editorCtx.insertImage({
								src: url,
								alt: '图像'
							})
						})
					})
				}
			})
		},
		fontSize(e) {
			const index = e.detail.value
			const fz = this.fontSizeRange[index] + 'px'
			this._format('fontSize', fz, '字体大小:' + fz)
		},
		cancel() {
			console.log("cancel")
		},
		reset() {
			this.htmlData = ''
			this.editorCtx.clear({
				success: (res) => {
					this.toast("重置成功~")
					this.$emit("reset")
				}
			})
		},
		saveAuto(){
			this.editorCtx.getContents({
				success: res => {
					if (res.errMsg == "ok") {
						this.htmlData = res.html
					}
					this.$emit("save", {
						htmlData: this.htmlData
					})
				}
			})
		},
		save() {
			let _this = this
			console.log("save")
			if (this.showPreview) {
				if (this.previewMode) {
					this.cancel()
				} else {
					this.showPreview = false
				}
			} else {
				this.editorCtx.getContents({
					success: res => {
						if (res.errMsg == "ok") {
							this.htmlData = res.html
							this.toast("保存成功")
							// console.log(this.htmlData)
						}
						this.$emit("save", {
							htmlData: this.htmlData
						})
					}
				})
			}

		},
		previewData: function(html) {
			this.htmlData = html.replace(/\<img/gi, '<img style="max-width:100%;height:auto"')
			this.showPreview = true
		},
		preview: function() {
			this.editorCtx.getContents({
				success: res => {
					this.previewData(res.html)
				}
			})

		}
	}
}
